:root {
  --main-black: #000;
  --dark-black: #131313;
  --main-white: #fcfcfc;
  --simple-white: #fff;
  --yellow: #f8d24e;
  --bggreen: #84a178;
  --icecreambg: #aec6a5;
  --icecoffebg: #efd478;
  --milkshakebg: #d6936d;
  --inputbg: #f8d24e1a;
  --ingredients-green: #9cbe8f;
  --button-hover-focus: #0a8817;
  --button-disabled: #ababab;
  --S-text-size: 12px;
  --M-text-size: 14px;
  --L-text-size: 18px;
  --XL-text-size: 20px;
  --XXL-text-size: 30px;
  --font-wieght-XS: 400;
  --font-wieght-S: 500;
  --font-wieght-M: 600;
  --font-weight-L: 700;
  --font-wieght-XL: 800;
  --font-wieght-XXL: 900;
  --font-standard: "Montserrat", sans-serif;
  --font-special: "Seymour One", sans-serif;
}

body {
  font-size: 16px;
  font-family: var(--font-standard);
  background-color: var(--bggreen);
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  text-decoration-thickness: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.container {
  min-width: 320px;
  max-width: 480px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop {
  height: 100%;
  width: 100%;
  z-index: 2;
  background-color: #0003;
  position: fixed;
  top: 0;
  left: 0;
}

.gallery-reviews-wrapper {
  background-image: linear-gradient(#84a278, #84a278 24px, #0000 24px), url("background_mobile.8b18d83f.jpg");
  background-position: center;
  background-size: cover;
  margin-top: 32px;
}

@media screen and (min-width: 768px) {
  .gallery-reviews-wrapper {
    min-height: 1122px;
    background-image: linear-gradient(#84a278, #84a278 164px, #0000 164px), url("background_mobile.8b18d83f.jpg");
    background-size: contain;
    margin-top: 60px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery-reviews-wrapper {
    min-height: 1110px;
    background-image: linear-gradient(#84a278, #84a278 95px, #0000 95px), url("background_mobile.8b18d83f.jpg");
    background-size: contain;
    margin-top: 80px;
  }
}

.hero-section {
  background-image: url("dots-mob-1x.6f70a6d4.png");
  background-position: 100% 100%;
  background-repeat: no-repeat;
}

.hero-title {
  width: 247px;
  font-family: var(--font-special);
  letter-spacing: 1.04px;
  text-transform: uppercase;
  color: var(--main-white);
  text-align: left;
  margin-top: 50px;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.hero-title > span {
  text-transform: uppercase;
  text-align: left;
  font-size: 34px;
  font-style: normal;
  font-weight: 400;
  line-height: 96.5%;
}

.hero-button {
  border: 2px solid var(--yellow);
  text-align: center;
  font-family: var(--font-standard);
  color: var(--main-black);
  background-color: #0000;
  border-radius: 8px;
  padding: 11px 26px;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.hero-button:hover {
  border: 2px solid var(--button-hover-focus);
}

.hero-button:focus {
  border: 2px solid var(--button-hover-focus);
  color: var(--main-white);
}

.hero-button:disabled {
  border: 2px solid var(--button-disabled);
}

.hero-link {
  color: var(--yellow);
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-decoration-line: underline;
}

.hero-link:hover {
  color: var(--button-hover-focus);
}

.hero-link:focus {
  color: var(--main-white);
}

.hero-link:disabled {
  color: var(--button-disabled);
}

.hero-button-link {
  align-items: center;
  gap: 12px;
  margin-top: 32px;
  margin-bottom: 19px;
  display: inline-flex;
}

.hero-number {
  color: var(--dark-black);
  text-align: right;
  font-family: var(--font-standard);
  letter-spacing: 1.12px;
  text-transform: uppercase;
  font-size: 28px;
  font-style: normal;
  font-weight: 900;
  line-height: normal;
}

.hero-cafe-text {
  color: var(--main-white);
  text-align: right;
  font-family: var(--font-standard);
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.hero-quantity-places, .hero-plate {
  display: none;
}

.hero-text {
  color: #fff;
  width: 165px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.4;
  display: none;
}

.hero-text-svg {
  display: none;
}

.hero-background {
  justify-content: end;
  display: flex;
}

.hero-icecream {
  width: auto;
  height: 217px;
  padding-right: 40px;
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .hero-section {
    max-height: 415px;
    max-width: 480px;
  }

  .hero-icecream {
    height: 267px;
    padding-right: 20px;
  }

  .hero-background {
    margin-top: -130px;
  }

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .hero-section {
      background-image: url("dots-mob-2x.fd522ff4.png");
    }
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .hero-section {
    background-image: url("dots-tab-1x.6d358330.png");
    background-position: bottom;
