    justify-content: end;
    gap: 20px;
    margin-bottom: -28px;
    display: flex;
  }

  .flex-cafes {
    flex-direction: column;
    gap: 4px;
    display: flex;
  }

  .hero-button-link {
    margin-top: 24px;
  }

  .hero-plate {
    height: 152px;
    width: auto;
    display: block;
  }

  .hero-background {
    margin-top: -316px;
  }

  .hero-icecream {
    height: 329px;
    padding-right: 72px;
  }

  .hero-text {
    display: block;
  }

  .hero-text-svg {
    flex-direction: column;
    gap: 29px;
    display: flex;
  }

  .hero-plate-text {
    align-items: center;
    gap: 32px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .hero-section {
    background-image: url("dots-desk-1x.b86eca2b.png");
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: auto 383px;
  }

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .hero-section {
      background-image: url("dots-desk-2x.62d2ade6.png");
    }
  }

  .hero-title-box {
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 45px;
    display: flex;
  }

  .hero-title {
    letter-spacing: 1.52px;
    text-transform: uppercase;
    width: 367px;
    margin-top: 0;
    font-size: 38px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .hero-link {
    padding: 12px 21px;
  }

  .hero-icecream {
    height: 560px;
    padding-right: 76px;
  }

  .hero-background {
    margin-top: -500px;
  }

  .hero-quantity-places {
    flex-direction: column;
    justify-content: end;
    gap: 16px;
    margin-bottom: -28px;
    display: flex;
  }

  .flex-cafes {
    flex-direction: column;
    gap: 4px;
    display: flex;
  }

  .hero-button-link {
    margin-top: 28px;
    margin-bottom: 45px;
  }

  .hero-plate {
    height: 225px;
    width: auto;
    display: block;
  }

  .hero-text {
    color: #fff;
    font-family: DM Sans;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    display: block;
  }

  .hero-text-svg {
    flex-direction: column;
    gap: 29px;
    display: flex;
  }

  .hero-plate-text {
    align-items: center;
    gap: 32px;
    display: flex;
  }

  .hero-number {
    text-align: right;
    letter-spacing: 1.84px;
    text-transform: uppercase;
    font-family: Montserrat;
    font-size: 46px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
  }

  .hero-cafe-text {
    text-align: right;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
}

#gallery-list {
  position: relative;
}

#slide1 {
  height: auto;
  display: block;
}

#slide2, #slide3 {
  height: auto;
  position: absolute;
  top: 0;
}

#slide1 {
  animation: 10s infinite fade-1;
}

#slide2 {
  animation: 10s infinite fade-2;
}

#slide3 {
  animation: 10s infinite fade-3;
}

@keyframes fade-1 {
  0% {
    opacity: 1;
  }

  33% {
    opacity: 0;
  }

  66% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fade-2 {
  0% {
    opacity: 0;
  }

  33% {
    opacity: 1;
  }

  66% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fade-3 {
  0% {
    opacity: 0;
  }

  33% {
    opacity: 0;
  }

  66% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

#gallery-list {
  margin-bottom: 80px;
}

.gallery-item {
  max-width: 100%;
  margin: auto;
}

.gallery-img {
  border-radius: 8px;
}

@media screen and (min-width: 768px) {
  #gallery-list {
    margin-bottom: 100px;
  }

  .gallery-item {
    max-width: 704px;
  }

  .gallery-img {
    border-radius: 18px;
  }
}

@media screen and (min-width: 1200px) {
  #gallery-list {
    margin-bottom: 140px;
  }

  .gallery-item, #slide1, #slide2, #slide3 {
    max-width: 1013px;
  }

  .gallery-img {
    border-radius: 18px;
  }
}

.swiper {
  width: 280px;
  height: auto;
  font-family: var(--font-standard);
  text-align: center;
  margin: 0 auto;
}

.swiper-wrapper {
  display: block;
}

.review-img {
  max-width: 85px;
  max-height: 85px;
  border-radius: 100%;
  margin: auto;
}

.review-opinion {
  background: var(--bggreen);
  color: var(--simple-white);
  font-size: var(--S-text-size);
  border-radius: 10px;
  margin: 32px auto;
  padding: 35px 39px;
  line-height: 2.55;
}

.review-name {
  font-size: var(--XL-text-size);
  font-weight: var(--font-wieght-S);
  line-height: 1.86;
}

@media screen and (min-width: 768px) {
  .swiper {
    width: 705px;
  }

  .review-opinion {
    font-size: var(--M-text-size);
    font-weight: var(--font-wieght-S);
    letter-spacing: .56px;
    border-radius: 24px;
    padding: 28px 80px;
    line-height: 1.86;
  }

  .review-img {
    max-width: 84px;
    max-height: 84px;
  }

  .review-name {
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .swiper {
    width: 968px;
  }

  .review-opinion {
    letter-spacing: .64px;
    padding: 43px 109px;
    font-size: 16px;
  }

  .review-img {
    max-width: 85px;
    max-height: 85px;
  }
}

#contacts {
  background-color: var(--bggreen);
  padding-bottom: 30px;
}

.contact-items-wrapper {
  width: auto;
  font-family: var(--font-standard);
  font-weight: var(--font-wieght-S);
  letter-spacing: .48px;
  color: var(--dark-black);
  font-size: var(--S-text-size);
  display: block;
}

.contact-item {
  max-width: 280px;
  background-color: var(--main-white);
  border-radius: 24px;
  margin: 16px auto;
  padding: 40px 32px 24px;
  list-style: none;
  box-shadow: 0 8px 30px #d414431a;
}

.contact-item-category {
  background-color: var(--yellow);
  text-transform: uppercase;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 4px 22px;
  line-height: 1.2;
}

.contact-item-paragraph {
  line-height: 1.86;
}

.contact-item-address {
  border: 0 solid #e1e1e1;
  border-bottom-width: 1px;
  margin-bottom: 24px;
  padding: 8px 0 24px;
}

.contact-item-address a {
  color: var(--milkshakebg);
  font-style: normal;
  line-height: 1.83;
  text-decoration: none;
}

.contact-item-address a:hover, .contact-item-address a:focus {
  text-decoration: underline;
}

.contact-time-wrapper {
  margin-bottom: 8px;
}

.contact-item-hours {
  color: var(--button-disabled);
  margin: 0 0 8px;
  line-height: 1.86;
}

.contact-info-wrapper {
  border: 0 solid #e1e1e1;
  border-top-width: 1px;
  margin-top: 24px;
  padding: 24px 0 0;
}

.contact-link-tel a, .contact-link-mail a {
  color: #131313;
  line-height: 1.6;
  text-decoration: none;
}

.contact-link-tel {
  margin-bottom: 6px;
}

.contact-btn-list {
  text-align: center;
  list-style: none;
  display: block;
}

.contact-btn-primary, .contact-btn-secondary, .modal-btn {
  width: 196px;
  padding: var(--S-text-size);
  font-family: Montserrat;
  font-size: var(--M-text-size);
  font-weight: var(--font-wieght-M);
  text-align: center;
  border: 2px solid var(--yellow);
  border-radius: 10px;
  line-height: 1.29;
}

.modal-btn {
  width: auto;
  margin-top: 24px;
  padding: 12px 34px;
}

.contact-btn-primary:hover, .contact-btn-secondary:hover, .modal-btn:hover {
  border-color: var(--button-hover-focus);
  cursor: pointer;
}

.contact-btn-primary:focus, .contact-btn-secondary:focus, .modal-btn:focus {
  color: #fff;
}

.contact-btn-primary:disabled, .contact-btn-secondary:disabled, .modal-btn:disabled {
  border-color: var(--button-disabled);
}

.contact-btn-primary, .modal-btn {
  background-color: var(--yellow);
}

.contact-btn-primary:hover, .modal-btn:hover {
  background-color: var(--button-hover-focus);
}

.contact-btn-secondary {
  background-color: #0000;
  margin-top: 16px;
}

.modal-body-locations, .modal-body-franchise {
  background-color: var(--main-white);
  border: 2px solid var(--yellow);
  width: calc(100% - 40px);
  max-width: 280px;
  border-radius: 10px;
  padding: 28px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.map-img {
  display: none;
}

.modal-body-locations p, .modal-body-franchise-header {
  font-family: var(--font-special);
  font-size: var(--L-text-size);
  font-weight: var(--font-wieght-XS);
  margin-bottom: 32px;
  line-height: 1.5;
}

.modal-body-franchise-header {
  max-width: 322px;
  text-align: left;
  margin: 0 auto 20px;
  font-size: 16px;
}

.modal-body-franchise-paragraph {
  font-family: var(--font-standard);
  line-height: 1.4;
  font-size: var(--M-text-size);
  text-align: left;
}

.franchise-form-wrapper {
  margin: 24px 0 0;
  display: block;
}

.franchise-form-wrapper input {
  margin-bottom: 12px;
}

.franchise-form-wrapper textarea {
  min-height: 64px;
}

.franchise-form-wrapper input, .franchise-form-wrapper textarea {
  max-width: 368px;
  width: 100%;
  border: 1px solid var(--bggreen);
  background: var(--inputbg);
  color: var(--dark-black);
  font-family: var(--font-standard);
  font-size: var(--S-text-size);
  border-radius: 10px;
  padding: 8px 12px 7px;
  line-height: 1.4;
}

.modal-close {
  fill: var(--dark-black);
  background-color: #0000;
  border: none;
  position: absolute;
  top: 12px;
  right: 12px;
}

.modal-close-icon, .modal-close-icon:hover {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-close-icon:hover, .modal-close-icon:focus {
  transform: rotate(360deg);
}

@media screen and (min-width: 768px) {
  .contact-items-wrapper {
    gap: 16px;
    margin: 0 0 40px;
    display: flex;
  }

  .contact-item {
    width: calc(33.3333% - 10.6667px);
