    padding: 40px;
    font-size: 16px;
  }

  .contact-item-category {
    line-height: 1.4;
  }

  .contact-item-address {
    margin-bottom: 32px;
    padding: 8px 0 32px;
  }

  .contact-info-wrapper {
    margin-top: 32px;
    padding-top: 32px;
  }
}

