:root {
  --main-black: #000;
  --dark-black: #131313;
  --main-white: #fcfcfc;
  --simple-white: #fff;
  --yellow: #f8d24e;
  --bggreen: #84a178;
  --icecreambg: #aec6a5;
  --icecoffebg: #efd478;
  --milkshakebg: #d6936d;
  --inputbg: rgba(248, 210, 78, 0.1);
  --ingredients-green: #9cbe8f;
  --button-hover-focus: #0a8817;
  --button-disabled: #ababab;
  --S-text-size: 12px;
  --M-text-size: 14px;
  --L-text-size: 18px;
  --XL-text-size: 20px;
  --XXL-text-size: 30px;
  --font-wieght-XS: 400;
  --font-wieght-S: 500;
  --font-wieght-M: 600;
  --font-weight-L: 700;
  --font-wieght-XL: 800;
  --font-wieght-XXL: 900;
  --font-standard: 'Montserrat', sans-serif;
  --font-special: 'Seymour One', sans-serif;
}
body {
  font-size: 16px;
  font-family: var(--font-standard);
  background-color: var(--bggreen);
}
* {
  list-style: none;
  margin: 0;
  padding: 0;
  text-decoration: 0;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
button {
  cursor: pointer;
}
a {
  text-decoration: none;
}
.container {
  min-width: 320px;
  max-width: 480px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}
@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}
.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #00000033;
  z-index: 2;
}

.gallery-reviews-wrapper {
  background-image: linear-gradient(#84a278, #84a278 24px, rgba(0, 0, 0, 0) 24px),
    url(/src/images/products/background_mobile.jpg);
  background-position: center;
  background-size: cover;
  margin-top: 32px;
}

@media screen and (min-width: 768px) {
  .gallery-reviews-wrapper {
    min-height: 1122px;
    background-image: linear-gradient(#84a278, #84a278 164px, rgba(0, 0, 0, 0) 164px),
      url(/src/images/products/background_mobile.jpg);
    background-size: contain;
    margin-top: 60px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery-reviews-wrapper {
    min-height: 1110px;
    background-image: linear-gradient(#84a278, #84a278 95px, rgba(0, 0, 0, 0) 95px),
      url(/src/images/products/background_mobile.jpg);
    background-size: contain;
    margin-top: 80px;
  }
}
